
























































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import CStatistics from '@/components/links/CStatistics.vue';
import MathUtil from '@/typescript/MathUtil.ts'
import StatisticsCalc from '@/typescript/StatisticsCalc.ts'

@Component({
  components: {
    CStatistics,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '代表値',
    },
  },
})
export default class Average extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataText: string = '';
  private dataSelectedSampleExampleIndex: number = 0;
  private dataSampleExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'standard_normal_distribution',
      caption: '標準正規分布',
      content: '-0.2403,-0.3525,0.0218,-0.0259,-0.3045,-0.3117,-2.2695,0.5960,-0.1381,-0.0539,-1.3129,-0.7382,0.3343,1.3204,0.7149,-0.4995,0.2579,-0.2385,-0.3667,-1.5362,0.6342,0.9549,0.3145,0.4613,-2.3169,1.9595,0.6941,0.1815,-1.1364,1.0000,-0.2320,-1.1423,0.4157,-0.6974,0.3588,-2.0092,0.2415,-0.0078,-0.3803,0.1769,0.5448,1.6442,-0.6668,-0.9353,2.2669,0.2270,0.4189,-0.2930,1.0448,0.2927,1.4437,0.0211,-0.2644,-0.2817,-0.7384,0.2550,0.1388,1.1511,1.3556,-0.1319,-1.2879,1.9756,-1.6088,-0.0118,-0.1031,1.0682,0.3041,-0.9554,1.6568,0.5224,0.0688,2.5085,0.2227,0.2298,0.5894,-0.5437,-2.9925,-0.4136,-1.9917,-0.5716,1.2492,0.6778,0.6859,-0.2613,-0.8280,0.3812,0.3479,-0.1187,0.0703,-0.6089,1.8891,1.0930,-0.7582,-0.9539,-0.7810,0.6767,-0.2121,-1.1378,0.5313,0.4247,',
    },
    {
      name: 'test',
      caption: 'センター試験（国語）',
      content: '144 81 172 177 92 65 133 112 84 76 104 96 122 97 85 100 130 86 183 87 137 91 106 119 107 173 100 110 99 81 79 61 18 136 96 165 97 156 97 152 92 123 106 61 178 137 98 88 70 154',
    },
  ];
  private dataSamples: number[] = [];
  private dataSampleSize: string = '';
  private dataMaximum: string = '';
  private dataMinimum: string = '';
  private dataMean: string = '';
  private dataMode: string = '';
  private dataMedian: string = '';
  private dataVariance: string = '';
  private dataStandardDeviation: string = '';

  private created(): void {
  }

  // Methods
  private listenOnSampleExampleSelect($event: Event): void {
    this.dataSelectedSampleExampleIndex = Number(<any>$event);
    this.dataText = this.dataSampleExamples[this.dataSelectedSampleExampleIndex].content;
  }
  private listenInput($event: Event): void {
    this.dataText = (<HTMLInputElement>(<any>$event).target).value;
  }
  private calcStatistics(): void {
    this.dataSamples.length = 0;
    let samples: string[] | null = this.dataText.match(/(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?/g);
    if (samples !== null) {
      samples.forEach((value: string): void => {
        this.dataSamples.push(Number(value));
      });
      this.dataSampleSize = String(MathUtil.toLocaleString(this.dataSamples.length));
      this.dataMean = String(MathUtil.toLocaleString(StatisticsCalc.calcMean(this.dataSamples)));
      let mode: number | null = StatisticsCalc.calcMode(this.dataSamples);
      this.dataMode = mode === null ? '--' : String(mode);
      this.dataMedian = String(MathUtil.toLocaleString(StatisticsCalc.calcMedian(this.dataSamples)));
      this.dataMaximum = String(StatisticsCalc.calcMaximum(this.dataSamples));
      this.dataMinimum = String(StatisticsCalc.calcMinimum(this.dataSamples));
      this.dataVariance = String(MathUtil.toLocaleString(StatisticsCalc.calcVariance(this.dataSamples)));
      this.dataStandardDeviation = String(MathUtil.toLocaleString(StatisticsCalc.calcStandardDeviation(this.dataSamples)));
    }
  }
}
